#page-wrapper #note {
  margin: 0 140px;
  position: fixed;
  z-index: 9999;
  top: 0;
  right: 0;
  opacity: 0.9;
  font-weight: bold;
  border-radius: 0;
  display: block;
  transform: translateY(-54px);
  animation: slideDown 5s 1s 1 ease;
  -webkit-transform: translateY(-54px);
  -webkit-animation: slideDown 5s 1s 1 ease;
  -moz-transform: translateY(-54px);
  -moz-animation: slideDown 5s 1s 1 ease;
}

@keyframes slideDown {
  0%, 100% {
    transform: translateY(-54px);
  }
  10%, 90% {
    transform: translateY(0px);
  }
}

@-webkit-keyframes slideDown {
  0%, 100% {
    -webkit-transform: translateY(-54px);
  }
  10%, 90% {
    -webkit-transform: translateY(0px);
  }
}

@-moz-keyframes slideDown {
  0%, 100% {
    -moz-transform: translateY(-54px);
  }
  10%, 90% {
    -moz-transform: translateY(0px);
  }
}

